import React, { Component } from "react"

import "./index.scss";
import { Carousel } from "antd-mobile";
import { Head } from "../../components/head"
import {history} from "~"


class Home extends Component {
    constructor() {
        super();
        this.state = {
            banner: [
                { img: "http://img.shop.wanmei.com/upload/ad/2018-08-30/ff06a52600b74bd3b15a64c279d857a3.jpg" },
                { img: 'http://img.shop.wanmei.com/upload/ad/2019-11-26/19e0dc1ada754c2988a4e5d8d777ada3.jpg' },
                { img: 'http://img.shop.wanmei.com/upload/ad/2019-11-26/810f74fb4dd54b11a82d42d91e70c0be.jpg' }
            ]
        }
    }


    // goTop=()=>{
    //     document.body.scrollTop=0;
    //     document.documentElement.scrollTop = 0;
    // }

    render() {
        return (
            <div className="home" style={{overflow:"scroll"}}>
                <Head title="首页"></Head>
                <Carousel
                    autoplay={true}
                    dots={true}
                    infinite
                    dotActiveStyle={{ backgroundColor: '#e90404' }}
                >
                    {
                        this.state.banner.map((val, i) => {
                            return (
                                <a key={i} href=""
                                    style={{ display: "inline-block", width: "100%", height: "400" }}
                                >
                                    <img
                                        src={val.img}
                                        style={{ width: '100%', height: "400" }}
                                        onLoad={() => {
                                            window.dispatchEvent(new Event("resize"));
                                        }}
                                    />
                                </a>
                            )
                        })
                    }
                </Carousel>
                <div className="banner">
                    <ul>
                        <li>
                            <img src="http://img.shop.wanmei.com/upload/ad/2017-07-20/f3f44bc57627415588a72d1627a13b4f.jpg" alt="" />
                            新品发售
                        </li>
                        <li>
                            <img src="http://img.shop.wanmei.com/upload/ad/2017-07-20/8dbfede746e84867bd76adcffc69fe84.jpg" alt="" />
                            热销推荐
                        </li>
                        <li>
                            <img src="http://img.shop.wanmei.com/upload/ad/2017-07-20/107fd58bcd944822b45cd0f714d66d2d.jpg" alt="" />
                            神秘商店
                        </li>
                        <li>
                            <img src="http://img.shop.wanmei.com/upload/ad/2017-07-20/17c12afa0dd34812a72962b3ae094418.jpg" alt="" />
                            折扣专区
                        </li>
                        <li>
                            <img src="http://img.shop.wanmei.com/upload/ad/2017-07-20/5f3c42441f854f58af78bd78cd0895a6.jpg" alt="" />
                            抽奖专区
                        </li>
                    </ul>
                </div>
                <div className="content">
                    <h3>特别推荐</h3>
                    <ul>
                        <li>
                            <img src="http://img.shop.wanmei.com/upload/product/2018-08-30/49c0b5109e4f407587f8ef9d4ad30e98_210.jpg" alt="" />
                            <p>梦间集-超大桌垫</p>
                            <p>￥75.00</p>
                        </li>
                        <li>
                            <img src="http://img.shop.wanmei.com/upload/product/2019-05-27/b2962a9c67d1466aa23b9c7132106206_210.jpg" alt="" />
                            <p>武林外传手游-限</p>
                            <p>￥199.00</p>
                        </li>
                        <li>
                            <img src="http://img.shop.wanmei.com/upload/product/2019-11-26/474aa050c8434175a972c387a2da2f23_210.jpg" alt="" />
                            <p>云梦四时歌-三星</p>
                            <p>￥68.00</p>
                        </li>
                    </ul>
                </div>
                <div className="goods">
                    <img src="http://img.shop.wanmei.com/upload/moduleProduct/2017-07-20/7065f5ffdf3742b4a6f1e75072c7194e.jpg" alt=""/>
                    <div>
                        <p>诛仙3-碧瑶等身抱枕</p>
                        <p>￥180.00</p>
                    </div>
                    <ul>
                        <li>
                            <p>诛仙3-碧瑶移动电源</p>
                            <p>￥79.00</p>
                        </li>
                        <li>
                            <p>诛仙3-树妖抱枕被</p>
                            <p>￥79.00</p>
                        </li>
                    </ul>
                </div>
                <div className="goods">
                    <img src="http://img.shop.wanmei.com/upload/moduleProduct/2017-07-20/7065f5ffdf3742b4a6f1e75072c7194e.jpg" alt=""/>
                    <div>
                        <p>诛仙3-碧瑶等身抱枕</p>
                        <p>￥180.00</p>
                    </div>
                    <ul>
                        <li>
                            <p>诛仙3-碧瑶移动电源</p>
                            <p>￥79.00</p>
                        </li>
                        <li>
                            <p>诛仙3-树妖抱枕被</p>
                            <p>￥79.00</p>
                        </li>
                    </ul>
                </div>
                <h3 style={{width:"100%",height:"42px",margin:"0 0 45px 0",lineHeight:"42px",textAlign:'center',borderTop:"1px solid #bfbfbf",borderBottom:"1px solid #bfbfbf",fontSize:"12px",color:"#999"}}>完美世界（北京）科技有限公司</h3>
                {/* <span id="top" onClick={this.goTop}></span> */}
            </div>
        )
    }
}

export default  Home
